import * as React from 'react';
import { useParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { Viewports } from '@miq/componentjs';

import { useCrudEmitter } from '../events';
import { ProductCardList } from '../Product/components';
import { useProductListRequest } from '../Product';

const ProductUpdateView = React.lazy(() => import('../Product/UpdateView'));

export default function OrderProductUpdateView() {
  const { ordSlug } = useParams();
  const { res = { data: { results: [] } }, ...rest } = useProductListRequest(ordSlug);

  useCrudEmitter({ onUpdate: rest.updateItem, onDestroy: rest.removeItem });

  const back = `/staff/store/orders/${ordSlug}/`;

  return (
    <Staff.View>
      <div className="d-grid grid-xl-4 gap-2">
        <div className="span-xl-3">
          <ProductUpdateView back={back} />
        </div>

        <Viewports.XLUp>
          <div style={{ position: 'relative' }}>
            <div style={{ height: '100%', overflowY: 'auto', position: 'absolute', width: '100%' }}>
              <ProductCardList items={res?.data?.results || []} itempath={back} />
            </div>
          </div>
        </Viewports.XLUp>
      </div>
    </Staff.View>
  );
}

const Scroll = ({ style, ...props }: React.ComponentPropsWithoutRef<'div'>) => (
  <div {...props} style={{ display: 'flex', position: 'fixed', overflow: 'hidden', height: '100vh', ...style }} />
);
Scroll.Column = ({ style, ...props }: React.ComponentPropsWithoutRef<'div'>) => (
  <div {...props} style={{ position: 'relative', height: '100%', overflowY: 'auto', ...style }} />
);
